@import url(https://fonts.googleapis.com/css?family=Manjari&display=swap);
html, :root{
  width: 100%;
}
body {
  margin: 0;
  width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Manjari", "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
nav *, nav{
  text-decoration: none;
  color: whitesmoke;
  
  box-sizing: border-box;  
}
.nav-container{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 5vw;
  background: #333;
}
.nav-container div:first-child{
  display: flex;
  align-items: center;
}
.nav-container img{
  width: 5vw;
  height: auto;
}
.nav-container a{
  margin: 0 10px;
}
.container{
  width: 80%;
  margin: 5vh auto;

  font-size-adjust: 0.6;
}

.boxes{
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;
}
.box{
  width: 25%;
  display: inline-block;
  box-sizing: border-box;
  padding: 10px;
}

.box *{
  text-align: center;
  margin: 0 auto;
}
.box img{
  display: block;
  
}

.search-app{
  padding: 100px;
}
body{
  background: #333;
  color: whitesmoke;
}

.square{
  width: 50px;
  height: 50px;
  border: 1px solid whitesmoke;
  margin: 2px;
  background: none;
  color: white;
  font-size-adjust: 0.8;
  box-sizing: border-box
  
}
.square:hover{
  cursor:pointer;
  background: whitesmoke;
  color: #333;
  -webkit-transition: 200ms all;
  transition: 200ms all;
}

.game-container{
  width: 30%;
  margin: 25vh auto;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.board > div{
  display: flex;
  flex-direction: row;
}

ol{
  display: flex;
  flex-direction: column;
}
